import React from "react";
import { Container, List, Segment } from "semantic-ui-react";

const Footer = () => (
  <Segment inverted vertical>
    <Container textAlign="center">
      <List horizontal inverted divided link>
        <List.Item as="a" href="#">
          {" "}
          Powered by hary.fun{" "}
        </List.Item>
      </List>
    </Container>
  </Segment>
);
export default Footer;



